.recipe-list-item {
    border: 1px solid black;
    padding: 20px;
    width: 100%;
}

.recipe-list-item.selected {
    background-color: yellow;
}

.recipe-list {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.recipe-list:nth-child(odd) {
    background-color: green;
}

.recipe-list:nth-child(even) {
    background-color: lightgreen;
}