.shopper .ingredient-list li {
    display: block;
}

.shopper-list-item, .shopper-tags {
    display: inline-block;
}

.shopper-list-items+.shopper-list-items {
    margin-top: 4px;
}

.shopper .buttons {
    margin: 4px 6px;
}

.shopper .buttons button+button {
    margin-left: 8px;
}

.shopper .ant-calendar-picker {
    margin-bottom: 4px;
}

.sort-popup .drag-list {
    list-style: none;
}

.sort-popup .drag-list-item {
    padding: 4px;
}

.sort-popup .drag-list-item:first-child .up, .sort-popup .drag-list-item:last-child .down {
    visibility: hidden;
}

.drag-list-item .up {
    margin-left: 6px;
    margin-right: 12px;
}
