.recipe-form .buttons {
    position: relative;
    margin-bottom: 1px;
}

.recipe-form .ant-btn-group+.ant-btn-group {
    margin-left: 5px;
}

.recipe-form .multi-chooser {
    margin: 0 0 15px 0;
}

.recipe-form .config {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-bottom: 3px;
}

.recipe-form .config>i {
    margin-left: 5px;
}

.recipe-form .config .setting-icon {
    font-size: 18px;
}

.recipe-form .instructions {
    margin-top: 15px;
}

.recipe-form .ant-table .ant-select {
    min-width: 150px;
    max-width: 450px;
}

.recipe-form .ant-table input {
    max-width: 200px;
}

.recipe-form .ant-form-item {
    margin-bottom: 5px;
}