.recipe-report {
    margin: 3px 10px;
    overflow: auto;
    height: calc(100% - 6px);
}

.recipe-report .recipe-name, .recipe-report .desc-header, .recipe-report .ing {
    font-weight: bold;
}

.recipe-report .desc-body {
    white-space: pre-wrap;
}

.recipe-report .recipe {
    display: inline-grid;
    grid-template-columns: auto auto;
}

.recipe-report .recipe>div {
    padding: 5px 10px;
}

.recipe-select {
    margin: 5px 10px;
}

.reports-layout {
    height: calc(100% - 48px);
}
