.ant-carousel .slick-slide {
  text-align: center;
  height: 160px;
  line-height: 160px;
  background: #364d79;
  overflow: hidden;
}

.ant-carousel .slick-slide h3 {
  color: #fff;
}

.events {
  list-style: none;
  margin: 0;
  padding: 0;
}

.events .ant-badge-status {
  overflow: hidden;
  white-space: nowrap;
  width: 100%;
  text-overflow: ellipsis;
  font-size: 12px;
}

.notes-month {
  text-align: center;
  font-size: 28px;
}

.notes-month section {
  font-size: 28px;
}

.planner-toolbar {
  display: flex;
  align-items: center;
  padding: 10px;
  background-color: lightgray;
  height: 42px;
}

.planner-toolbar>* {
  display: inline-block;
}

.planner-toolbar>input {
  width: 60px;
}

.planner-toolbar input[type=number]::-webkit-inner-spin-button, .planner-toolbar input[type=number]::-webkit-outer-spin-button {
  opacity: 1;
}

.planner-list {
  display: flex;
  flex-wrap: wrap;
  margin: 20px;
}

.planner-list ul {
  margin: 0 20px;
}
